footer {
  background-color: var(--very-dk-cyan);
  position: relative;
}
footer::before {
  content: "";
  background-image: url("../images/bg-footer-top-mobile.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 100%;
  height: 5rem;
  z-index: -1;
}
@media (min-width: 40em) {
  footer::before {
    background-image: url("../images/bg-footer-top-desktop.svg");
    width: 100%;
    height: 10.625rem;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  padding-top: 5rem;
  padding-bottom: 3.125rem;
  color: var(--white);
  font-weight: 600;
  text-align: left;
}
.footer__logo {
  display: inline-block;
}
.footer__header {
  font-family: var(--font-heading);
  font-size: 1.25rem;
  text-transform: uppercase;
}
.footer__content {
  margin-bottom: 1.875rem;
  line-height: 1.8;
}
.footer-form {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
}
@media (min-width: 71.875em) {
  .footer-form {
    flex-direction: row;
    gap: 1.875rem;
  }
}
.footer__label {
  display: none;
}
.footer__email {
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
}
@media (min-width: 71.875em) {
  .footer__email {
    flex: 2;
  }
}
.footer__submit {
  color: var(--white);
  background-color: var(--pink);
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
  font-weight: 400;
  transition: background-color 300ms ease-in-out;
}
.footer__submit:hover {
  cursor: pointer;
  background-color: var(--lt-pink);
}
@media (min-width: 71.875em) {
  .footer__submit {
    flex: 1;
  }
}
@media (min-width: 40em) {
  .footer {
    flex-direction: row;
    gap: 6.25rem;
  }
  .footer-item1 {
    order: 2;
  }
  .footer-item2 {
    order: 1;
  }
}
.footer__phonenum {
  display: block;
  margin-bottom: 0.625rem;
}
.footer__phonenum::before {
  content: url("../images/icon-phone.svg");
  margin-right: 1.25rem;
}
.footer__emailadd {
  display: block;
  margin-bottom: 1.875rem;
}
.footer__emailadd::before {
  content: url("../images/icon-email.svg");
  margin-right: 1.25rem;
}
.footer__links {
  display: inline-block;
  font-size: 1.875rem;
  color: var(--white);
}
.footer__links:not(:last-child) {
  margin-right: 0.9375rem;
}